<template>
  <div class="access-by-mnemonic-container">
    <div class="wrap">
      <div class="page-container">
        <div class="title">
          <h2>{{ $t('common.accessMyWallet') }}</h2>
          <h5>
            {{ $t('accessWallet.pleaseTypeInMnemonicPhrase') }}
            {{ $t('common.noWallet') }}
            <router-link :to="'/create-wallet'" class="nounderline">
              {{ $t('common.createANewWallet') }}
            </router-link>
          </h5>
        </div>
        <div class="contents">
          <div class="tools">
            <div class="value-switch noselect">
              <div class="sliding-switch">
                <label class="switch">
                  <input type="checkbox" />
                  <span
                    class="slider round"
                    @click="mnemonicValueBitSizeChange"
                  />
                </label>
                <div class="labels">
                  <span :class="[!mnemonic24 ? 'white' : '', 'label-left']"
                    >12</span
                  >
                  <span :class="[mnemonic24 ? 'white' : '', 'label-right']"
                    >24</span
                  >
                </div>
              </div>
            </div>
          </div>
          <form>
            <div class="phrases">
              <ul>
                <li v-for="index in mnemonicSize" :key="index">
                  <span>{{ index }}.</span>
                  <input
                    v-model="mnemonicPhrase[index - 1]"
                    :ref="`mnemonicInput${index - 1}`"
                    style="text-transform: lowercase;"
                    type="text"
                    name
                    class="inputWord"
                  />
                </li>
              </ul>
            </div>
            <div class="button-container">
              <b-btn
                :class="{ 'invalid-button': !validMnemonic }"
                class="mid-round-button-green-filled close-button"
                type="submit"
                @click.prevent="unlockWallet"
              >
                <i v-show="spinner" class="fa fa-spin fa-spinner fa-lg" />
                <span v-show="!spinner">
                  {{ validMnemonic ? 'Confirm' : 'Words Are Incorrect' }}
                </span>
              </b-btn>
            </div>
            <!-- <p v-show="error !== ''" class="error">{{ error }}</p> -->
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { MnemonicWallet } from '@/wallets';
import * as utils from '@/wallets/utils.js';
import { Toast } from '@/helpers';

export default {
  components: {},
  props: {
    hardwareWalletOpen: {
      type: Function,
      default: function() {}
    }
  },
  data() {
    return {
      mnemonicPhrase: new Array(this.mnemonicSize).fill(''),
      mnemonic24: false,
      mnemonicSize: 12,
      error: '',
      spinner: false,
      validMnemonic: false
    };
  },
  watch: {
    mnemonicPhrase(newVal) {
      if (newVal[0] !== ' ' && newVal[0].indexOf(' ') >= 0) {
        if (
          newVal[0].split(' ').length === 12 ||
          newVal[0].split(' ').length === 24
        ) {
          this.mnemonic24 = newVal[0].split(' ').length === 24;
          this.mnemonicSize = newVal[0].split(' ').length;
          this.mnemonicPhrase = newVal[0].split(' ');
        }
      }
    }
  },
  mounted() {
    function inputHandler() {
      if (this.mnemonicPhrase.length !== this.mnemonicSize) {
        this.validMnemonic = false;
        return;
      }

      const mnemonic = this.mnemonicPhrase
        .map(word => word.toLowerCase())
        .join(' ');

      if (utils.validateMnemonic(mnemonic)) {
        this.validMnemonic = true;
      } else {
        this.validMnemonic = false;
      }
    }

    document.querySelectorAll('.phrases .inputWord').forEach(
      function(inputEl) {
        inputEl.addEventListener('input', inputHandler.bind(this));
      }.bind(this)
    );
  },
  methods: {
    unlockWallet() {
      if (!this.validMnemonic) return;
      this.spinner = true;
      const mnemonic = this.mnemonicPhrase
        .map(word => word.toLowerCase())
        .join(' ');
      MnemonicWallet(mnemonic, '')
        .then(wallet => {
          this.error = '';
          this.hardwareWalletOpen(wallet);
        })
        .catch(e => {
          this.spinner = false;
          this.error = e;
          Toast.responseHandler(e, Toast.ERROR);
        });
    },
    mnemonicValueBitSizeChange() {
      this.mnemonic24 = !this.mnemonic24;
      this.mnemonic24 ? (this.mnemonicSize = 24) : (this.mnemonicSize = 12);
      this.mnemonicPhrase = new Array(this.mnemonicSize).fill('');
      this.validMnemonic = false;
    }
  }
};
</script>
<style lang="scss" scoped>
@import 'AccessByMnemonicContainer-desktop.scss';
@import 'AccessByMnemonicContainer-tablet.scss';
@import 'AccessByMnemonicContainer-mobile.scss';
</style>
